import React from 'react';
import { Formik, Form } from 'formik';
import { withRouter } from 'react-router-dom';
import * as yup from 'yup';

// Components
import SubmitBtn from '../add/submit-btn/SubmitBtn';
import BootstrapContainer from '../re-use/bootstrap-container/BootstrapContainer';
import AddContainer from '../re-use/add/AddContainer';
import AddItemContainer from '../re-use/add/AddItemContainer';
import Email from '../login/email/Email';
import Password from '../login/password/Password';
import Name from './name/Name';
import PageLink from '../re-use/page-link/PageLink';
import Number from './number/Number';
import Axios from 'axios';
import P from '../re-use/p/P';

const initialValues = {
	name: '',
	number: '',
	email: '',
	password: ''
};
const phoneRegExp = /(0|\+98)?([ ]|,|-|[()]){0,2}9[1|2|3|4]([ ]|,|-|[()]){0,2}(?:[0-9]([ ]|,|-|[()]){0,2}){8}/;
const validationSchema = yup.object({
	name: yup.string().typeError('نام وارد نشده').max(10, 'نام باید کمتر از 10 حروف باشد').required('نام وارد نشده'),
	number: yup
		.string()
		.typeError('شماره وارد نشده')
		.matches(phoneRegExp, 'شماره موبایل صحیح نیست')
		.required('شماره وارد نشده'),
	email: yup.string().typeError('ایمیل وارد نشده').email('ایمیل درست وارد نشده').required('ایمیل وارد نشده'),
	password: yup
		.string()
		.typeError('رمز حساب انتخاب نشده')
		.max(10, 'رمز حساب باید کمتر از 10 حروف باشد')
		.required('رمز حساب انتخاب نشده')
});

function SignUp(props) {
	const [ loading, setLoading ] = React.useState(false);
	const [ axiosErr, setAxiosErr ] = React.useState(false);
	const [ err, setErr ] = React.useState('');

	const onSubmit = (e) => {
		setLoading(true);
		Axios.post('https://ravid-app.herokuapp.com/postUser', e)
			.then((res) => {
				localStorage.setItem('token', res.data.token);
				localStorage.setItem('number', e.number);
				localStorage.setItem('id', res.data.id);
			})
			.then(() => {
				setLoading(false);
				setAxiosErr(false);
			})
			.then(() => {
				props.history.push('/');
			})
			.catch((e) => {
				if (e.message === 'Network Error') {
					setErr('از اتصال به اینترنت مطمئن شوید');
				} else {
					setErr('این حساب قبلا ثبت شده');
				}
				setAxiosErr(true);
				setLoading(false);
			});
	};

	return (
		<Formik initialValues={initialValues} onSubmit={onSubmit} validationSchema={validationSchema}>
			{(props) => {
				return (
					<Form>
						<BootstrapContainer class="mt-5">
							<AddContainer>
								<AddItemContainer class={`align-items-center`}>
									<Name props={props} />
								</AddItemContainer>
							</AddContainer>
							<AddContainer>
								<AddItemContainer class={`align-items-center`}>
									<Number props={props} />
								</AddItemContainer>
							</AddContainer>
							<AddContainer>
								<AddItemContainer class={`align-items-center`}>
									<Email props={props} />
								</AddItemContainer>
							</AddContainer>
							<AddContainer>
								<AddItemContainer class={`align-items-center`}>
									<Password props={props} />
								</AddItemContainer>
							</AddContainer>
						</BootstrapContainer>
						<SubmitBtn loading={loading} props={props} text={`ایجاد حساب`} />
						{axiosErr ? <P class="text-danger text-center mt-2">{err}</P> : null}
						<PageLink text={`حساب کاربری دارید ؟`} link={`ورود به حساب`} to="/login" />
					</Form>
				);
			}}
		</Formik>
	);
}

export default withRouter(SignUp);
