import React from 'react';
import { ErrorMessage } from 'formik';
import { Input } from 'antd';

import classes from '../name/Name.module.scss';
import P from '../../re-use/p/P';

function Number(props) {
	const handleErr = () => {
		if (props.props.touched.number && props.props.errors.number) {
			return 'border border-danger';
		} else {
			return '';
		}
	};
	const cls = handleErr();

	const hdn = (e) => {
		if (e.target.value.length < 12) {
			props.props.setFieldValue('number', e.target.value);
		}
	};
	const handleBlur = () => {
		props.props.setFieldTouched('number');
	};

	return (
		<div>
			<Input
				style={{ direction: 'rtl' }}
				className={`${classes.name} ${cls}`}
				placeholder={`شماره مثل 09371101212`}
				onChange={hdn}
				onBlur={handleBlur}
				value={props.props.values.number}
			/>
			<P class="mt-2 text-danger mb-0">
				<ErrorMessage name="number" />
			</P>
		</div>
	);
}

export default Number;
