import React from 'react';
import { Input } from 'antd';
import { ErrorMessage } from 'formik';
import classes from './Name.module.scss';
import P from '../../re-use/p/P';

function Name(props) {
	const handleErr = () => {
		if (props.props.touched.name && props.props.errors.name) {
			return 'border border-danger';
		} else {
			return '';
		}
	};
	const cls = handleErr();
	return (
		<div>
			<Input
				style={{ direction: 'rtl' }}
				onBlur={() => props.props.setFieldTouched('name')}
				onChange={(e) => props.props.setFieldValue('name', e.target.value)}
				props={props}
				placeholder={`نام`}
				className={`${classes.name} ${cls}`}
			/>
			<P class="p-0 m-0 text-danger mt-2">
				<ErrorMessage name="name" />
			</P>
		</div>
	);
}

export default Name;
